.header-wrapper {
  position: fixed;
  left: 0.5rem;
  right: 0.5rem;
  height: 70px; /* altura do header */
  background-color: #0b121e;
  z-index: 999; /* atr√°s do header */
}

header {
  position: fixed;
  top: 0.8rem;
  left: 0.5rem; /* espa√ßo √† esquerda */
  right: 0.5rem; /* espa√ßo √† direita */
  padding: 1rem 2rem;
  background-color: #202b3c;
  border-radius: 20px;
  z-index: 1000;
}

header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
}

/* Logo */
.logo {
  font-size: 1.8rem;
  font-weight: bold;
}

.link-icon {
  margin-right: 6px;
}

.logo-img {
  width: 40px; /* ajusta o tamanho conforme necess√°rio */
  height: 40px;
  object-fit: contain;
  vertical-align: middle;
}

.logo span {
  font-size: 1.6rem; /* tamanho do texto */
  font-weight: 600; /* mais espesso para destaque */
  color: #d2d9e2; /* branco (podes mudar se quiser contraste) */
  letter-spacing: 1px; /* espa√ßamento entre letras */
  text-transform: uppercase; /* opcional ‚Äî todas mai√∫sculas */
  margin-left: 1rem;
}

/* Links da navbar */
.nav-links {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}

.nav-links a {
  display: flex; /* alinha √≠cone + texto */
  justify-content: center;
  align-items: center; /* verticalmente centrado */
  gap: 0.4rem; /* espa√ßo menor entre √≠cone e texto */
  text-decoration: none;
  color: #d2d9e2;
  font-weight: 500;
  transition: background-color 0.2s, color 0.2s;
  padding: 6px 12px; /* reduzido para ajustar propor√ß√£o */
  margin: 0 6px;
  border-radius: 12px;
  background-color: rgba(255, 255, 255, 0.1);
  font-size: 0.9rem; /* texto ligeiramente menor */
}

.nav-links a:hover {
  background-color: #0b121e;
  color: #d2d9e2;
  text-decoration: none; /* remove underline */
}

.nav-links a.active {
  font-weight: 600;
  background-color: #10161fc4;
}
.nav-links a img.nav-icon {
  width: 18px; /* reduzido para caber melhor no bot√£o */
  height: 18px;
  object-fit: contain;
}
/* Barra de pesquisa */
.search-form {
  display: flex;
  gap: 0.5rem;
}

.search-form input {
  padding: 0.4rem 0.6rem;
  border-radius: 5px;
  border: none;
  outline: none;
  background-color: #d2d9e2;
}

.search-form button {
  padding: 0.4rem 0.8rem;
  border: none;
  border-radius: 5px;
  background-color: #374150;
  color: #d2d9e2;
  cursor: pointer;
  transition: background-color 0.2s;
}

.search-form button:hover {
  background-color: #0b121e;
}

/* Hamburger menu */
.hamburger {
  display: none;
  font-size: 2rem;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}

/* === RESPONSIVIDADE === */

/* Telem√≥veis at√© 768px */
@media (max-width: 768px) {
  .hamburger {
    display: block;
  }

  .nav-links {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background-color: #0b121e;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
    max-height: 0;
    transition: max-height 0.5s ease-in-out;
    padding: 0;
    margin-top: 0.5rem;
    box-sizing: border-box;
    border-radius: 10px;
  }

  /* Quando o menu est√° aberto */
  .nav-links.open {
    max-height: 400px; /* ‚¨ÜÔ∏è aumenta este valor conforme o n¬∫ de links */
    padding-bottom: 1rem; /* ‚úÖ cria o espa√ßo vazio no fundo */
  }

  .nav-links a {
    width: 100%; /* largura ajustada e centrada */
    text-align: center;
    padding: 0.5rem 0;
  }

  .search-form {
    width: 100%;
    max-width: 300px; /* opcional: n√£o fica enorme em telas grandes */
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0.3rem auto; /* centralizado */
  }

  .search-form input,
  .search-form button {
    width: 100%; /* ocupa toda a largura do form */
    box-sizing: border-box; /* inclui padding e border na largura */
    padding: 0.5rem; /* padding igual para ambos */
    margin-bottom: 0.5rem; /* espa√ßamento entre input e bot√£o */
  }
}

/* Tablets 769px at√© 1024px */
@media (min-width: 769px) and (max-width: 1024px) {
  .logo {
    font-size: 1.9rem;
  }

  .nav-links {
    flex-direction: row;
  }
}

/* Desktop acima de 1024px */
@media (min-width: 1025px) {
  .logo {
    font-size: 2rem;
  }
}
